import React, { useEffect } from 'react';
import { IonPage, IonHeader, IonToolbar, IonTitle, IonContent, IonButton } from '@ionic/react';
import { signInWithGoogle, onAuthStateChangedListener } from '../firebase';
import { useHistory } from 'react-router-dom';

const Login = ({ onLoginSuccess }) => {
  const history = useHistory();

  // Check if the user is already logged in
  useEffect(() => {
    const unsubscribe = onAuthStateChangedListener((user) => {
      if (user) {
        console.log('User is already logged in:', user);
        onLoginSuccess(user);
        history.push('/dashboard'); // Redirect to dashboard after login
      }
    });

    return () => unsubscribe(); // Cleanup listener on unmount
  }, [history, onLoginSuccess]);

  const handleGoogleLogin = async () => {
    try {
      const user = await signInWithGoogle();
      console.log('User signed in:', user);
      onLoginSuccess(user);
      history.push('/dashboard'); // Redirect to dashboard after login
    } catch (error) {
      console.error('Login failed:', error);
      alert('Login failed. Please try again.');
    }
  };

  return (
    <IonPage>
      <IonHeader>
        <IonToolbar>
          <IonTitle>Login</IonTitle>
        </IonToolbar>
      </IonHeader>
      <IonContent className="ion-padding">
        <h2>Welcome to FitPro</h2>
        <IonButton expand="block" onClick={handleGoogleLogin}>
          Sign in with Google
        </IonButton>
      </IonContent>
    </IonPage>
  );
};

export default Login;