import React, { useEffect, useState } from 'react';
import { IonApp, IonRouterOutlet } from '@ionic/react';
import { IonReactRouter } from '@ionic/react-router';
import { Route, Redirect } from 'react-router-dom';
import { initializeDatabase } from './database/database';
import Login from './pages/Login';
import Dashboard from './pages/Dashboard';
import Workouts from './pages/Workouts';
import ProtectedRoute from './components/ProtectedRoute';
import { onAuthStateChangedListener } from './firebase';

import '@ionic/react/css/core.css';

const App = () => {
  const [user, setUser] = useState(null); // Manage logged-in user state
  const [dbInitialized, setDbInitialized] = useState(false); // Track database initialization

  // Initialize database
  useEffect(() => {
    const initDB = async () => {
      try {
        await initializeDatabase();
        setDbInitialized(true); // Mark database as initialized
        console.log('Database initialized successfully.');
      } catch (error) {
        console.error('Error initializing database:', error);
      }
    };
    initDB();
  }, []);

  // Track user authentication state
  useEffect(() => {
    const unsubscribe = onAuthStateChangedListener((user) => {
      setUser(user); // Update user state
    });

    return () => unsubscribe(); // Cleanup listener on unmount
  }, []);

  const handleLoginSuccess = (userData) => {
    setUser(userData);
  };

  if (!dbInitialized) {
    return <div>Loading...</div>;
  }

  return (
    <IonApp>
      <IonReactRouter>
        <IonRouterOutlet>
          {/* Public routes */}
          <Route path="/login" render={() => <Login onLoginSuccess={handleLoginSuccess} />} exact />

          {/* Protected routes */}
          <ProtectedRoute path="/dashboard" component={Dashboard} exact user={user} />
          <ProtectedRoute path="/workouts" component={Workouts} exact user={user} />

          {/* Redirects */}
          <Route exact path="/">
            {user ? <Redirect to="/workouts" /> : <Redirect to="/login" />}
          </Route>
        </IonRouterOutlet>
      </IonReactRouter>
    </IonApp>
  );
};

export default App;