import { CapacitorSQLite } from '@capacitor-community/sqlite';
import { Capacitor } from '@capacitor/core';

const dbName = 'fitpro.db';

// Helper function to check if the platform is web
const isWebPlatform = () => Capacitor.getPlatform() === 'web';

// Function to initialize the database
export const initializeDatabase = async () => {
  try {
    if (isWebPlatform()) {
      console.warn('SQLite is not supported on the web. IndexedDB fallback needed.');
      return;
    }

    const db = await CapacitorSQLite.open({ database: dbName });
    console.log('Database opened successfully.');

    // Create tables if they don't exist
    await db.execute({
      statements: `
        CREATE TABLE IF NOT EXISTS workouts (
          id INTEGER PRIMARY KEY AUTOINCREMENT,
          name TEXT NOT NULL,
          archived INTEGER DEFAULT 0
        );

        CREATE TABLE IF NOT EXISTS exercises (
          id INTEGER PRIMARY KEY AUTOINCREMENT,
          name TEXT NOT NULL,
          primary_muscle_group TEXT,
          secondary_muscle_group TEXT,
          notes TEXT
        );

        CREATE TABLE IF NOT EXISTS workout_exercises (
          id INTEGER PRIMARY KEY AUTOINCREMENT,
          workout_id INTEGER,
          exercise_id INTEGER,
          sets INTEGER,
          reps INTEGER,
          weight REAL,
          notes TEXT,
          FOREIGN KEY (workout_id) REFERENCES workouts(id),
          FOREIGN KEY (exercise_id) REFERENCES exercises(id)
        );

        CREATE TABLE IF NOT EXISTS logs (
          id INTEGER PRIMARY KEY AUTOINCREMENT,
          workout_exercise_id INTEGER,
          date DATETIME DEFAULT CURRENT_TIMESTAMP,
          sets INTEGER,
          reps INTEGER,
          weight REAL,
          notes TEXT,
          FOREIGN KEY (workout_exercise_id) REFERENCES workout_exercises(id)
        );
      `,
    });

    console.log('Tables created or already exist.');
  } catch (error) {
    console.error('Error initializing database:', error);
    throw error;
  }
};

// Function to add a workout
export const addWorkout = async (workout) => {
  try {
    if (isWebPlatform()) {
      console.warn('SQLite not supported on web. Add function disabled.');
      return;
    }

    const db = await CapacitorSQLite.open({ database: dbName });
    await db.run({
      statement: `INSERT INTO workouts (name, archived) VALUES (?, ?);`,
      values: [workout.name, workout.archived || 0],
    });

    console.log('Workout added:', workout.name);
  } catch (error) {
    console.error('Error adding workout:', error);
    throw error;
  }
};

// Function to fetch all workouts
export const getAllWorkouts = async () => {
  try {
    if (isWebPlatform()) {
      console.warn('SQLite not supported on web. Returning mock data.');
      return [];
    }

    const db = await CapacitorSQLite.open({ database: dbName });
    const result = await db.query({
      statement: `SELECT * FROM workouts;`,
    });

    console.log('Fetched workouts:', result);
    return result.values || [];
  } catch (error) {
    console.error('Error fetching workouts:', error);
    throw error;
  }
};

// Function to delete a workout
export const deleteWorkout = async (id) => {
  try {
    if (isWebPlatform()) {
      console.warn('SQLite not supported on web. Delete function disabled.');
      return;
    }

    const db = await CapacitorSQLite.open({ database: dbName });
    await db.run({
      statement: `DELETE FROM workouts WHERE id = ?;`,
      values: [id],
    });

    console.log(`Workout with ID ${id} deleted.`);
  } catch (error) {
    console.error('Error deleting workout:', error);
    throw error;
  }
};

